import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

import Menu1Page from '@/components/pages/Menu1Page'
import Menu2Page from '@/components/pages/Menu2Page'
import Menu3Page from '@/components/pages/Menu3Page'
import Menu4Page from '@/components/pages/Menu4page'

const MainPageTmp = { template: '<div>메인화면</div>' }
const ErrorPage = { template: '<div>경로 에러!</div>' }

export default new Router({
  mode: 'history',
  routes: [
    // { path: '*', component: ErrorPage }, // 지정 외의 경로 이동시 에러페이지 이동
    {
      path: '/',
      name: 'IndexPage',
      component: MainPageTmp,
    },
    { 
      path: '/menu/1', 
      name: 'MenuPage1',
      component: Menu1Page
    },
    { 
      path: '/menu/2', 
      name: 'MenuPage2',
      component: Menu2Page
    },
    { 
      path: '/menu/3', 
      name: 'MenuPage3',
      component: Menu3Page
    },
    { 
      path: '/menu/4', 
      name: 'MenuPage4',
      component: Menu4Page
    },
  ]
})



