<template lang="html">
    <div>
      {{ component }}
    </div>
</template>

<script>
export default {
  created: function () {
    const id = this.$route.params.id
    this.$http.delete(`http://localhost:8090/api/user/delete/1`)
    .then((response) => {
      this.component = response.data;
    })
  },
  data: function () {
    return {
      component: {}
    }
  }
}
</script>

<style lang="">
</style>



