<template lang="html">
    <div>
      {{ component }}
    </div>
</template>
<script>
export default {
  created: function () {
    const id = this.$route.params.id
    let form = new FormData();
    form.append('name', 'lee');
    form.append('password', 'pwd1234');
    form.append('nickname', 'wickies');
    form.append('email', 'wickies@naver.com');    
    this.$http.post(`http://localhost:8090/api/user/insert`, form)
    .then(res => {
      this.component = res.data;
    })
  },
  data: function () {
    return {
      component: {}
    }
  }
}
</script>

<style lang="">
</style>



