<template lang="html">
  <div>
    <div v-if="!!this.component.id">
      ID: {{ component.id }} <br/>
      Name: {{ component.name }} <br/>
      Password: {{ component.password }} <br/>
      NickName: {{ component.nickname }} <br/>
      E-Mail: {{ component.email }} <br/>
      In_Date: {{ component.in_date }} <br/>
      Premium: {{ component.premium }} <br/>
    </div>
    <div v-else> {{ component }} </div>
  </div>
</template>

<script>
export default {
  created: function () {
    const id = this.$route.params.id
    this.$http.get(`http://localhost:8090/api/user/select/1`)
    .then((response) => {
      this.component = response.data;
    })
  },
  data: function () {
    return {
      component: {}
    }
  }
}
</script>

<style lang="">
</style>



