<template>
  <div id="app">
    <TopPanel></TopPanel>
    <MenuPanel></MenuPanel>
    <MainPanel></MainPanel>
    <FooterPanel></FooterPanel>
  </div>
</template>

<script>
import TopPanel from './components/main/TopPanel.vue'
import MenuPanel from './components/main/MenuPanel'
import MainPanel from './components/main/MainPanel'
import FooterPanel from './components/main/FooterPanel'

export default {
  name: 'App',
  components: {
    'TopPanel' : TopPanel,
    'MenuPanel' : MenuPanel,
    'MainPanel' : MainPanel,
    'FooterPanel' : FooterPanel,
  },
  render(component) { 
    return component(this.ViewComponent)
  }
}
</script>

<style>
.body {
  width:100%;
  height:100vh;
  text-align: center;
}
</style>



