<template lang="html">
    <div>
      {{ component }}
    </div>
</template>

<script>
export default {
  created: function () {
    const id = this.$route.params.id
    let form = new FormData();
    form.append('name', 'kim');
    form.append('password', '000000000');
    form.append('nickname', 'kim');
    form.append('email', 'kim@naver.com');  
    let form2 =new FormData();
    form2.append('name', 'lee');
    form2.append('password', 'pwd1234');
    form2.append('nickname', 'wickies');
    form2.append('email', 'wickies@naver.com');    
    this.$http.post(`http://localhost:8090/api/user/update/1`, form)
    .then((response) => {
      this.component = response.data;
    })
  },
  data: function () {
    return {
      component: {}
    }
  }
}
</script>

<style lang="">
</style>



